














import { Component, Vue, Prop } from 'vue-property-decorator'
import HelloWorld from '@/components/HelloWorld.vue' // @ is an alias to /src

@Component({
  components: {
    HelloWorld
  }
})
export default class Home extends Vue {
  hello : string = 'Hello World!'
  msg = 123
  $createDialog : any
  // 声明周期钩子
  model = { name: '', password: '' }
  fields =[{ type: 'input', modelKey: 'name', label: '账号', props: { placeholder: '请输入' }, rules: { required: true } }, { type: 'input', modelKey: 'password', label: '密码', props: { placeholder: '请输入', type: 'password' }, rules: { required: true } }]
  valid = undefined
  validity = {}
  options = {}
  mounted () {
    console.log('钩子')
  }

  @Prop({ default: 'default value' }) readonly propB!: string

  // 计算属性
  get computedMsg () {
    return 'computed ' + this.hello + this.propB
  }
  showAlert () {
    this['$createDialog']({
      type: 'alert',
      title: '我是标题',
      content: '我是内容',
      icon: 'cubeic-alert'
    }).show()
  }
  validateHandler (result : any) {
    this.validity = result.validity
    this.valid = result.valid
    console.log('validity', result.validity, result.valid, result.dirty, result.firstInvalidFieldIndex)
    return false
  }
  submitHandler (e : any) {
    e.preventDefault()
    let pass = this.model.password
    let user = this.model.name
    this.$store.dispatch('login', { user: user, pass: pass }).then((response) => {
      if (!response.success) {
        this['$createDialog']({
          type: 'alert',
          content: '账号或密码错误',
          icon: 'cubeic-wrong'
        }).show()
      }
    })
  }
}
